<script>
	export default {
		onLaunch: async function(e) {
			console.log('App Launch')
			uni.hideTabBar({})
			let that = this
			let config = await uniCloud.callFunction({
				name: 'config_search',
				data: {
					'keys': ['writeShow', 'systemShow', 'videoUnitId', 'customUnitId', 'qqRewardedVideoUnitId',
						'qqUnitId', 'qqBannerUnitId', 'compressPicture', 'isShenHe','writeExam', 'templateId', 
						'wxRewardedVideoUnitId','bannerUnitId', 'indexUiType','appName'
					]
				},
			})
			if (config.result.success) {
				console.log('store data:', config.result.data)
				that.$u.vuex('vuex_writeShow', config.result.data[0])
				that.$u.vuex('vuex_systemShow', config.result.data[1])
				that.$u.vuex('vuex_videoUnitId', config.result.data[2])
				that.$u.vuex('vuex_customUnitId', config.result.data[3])
				that.$u.vuex('vuex_qqRewardedVideoUnitId', config.result.data[4])
				that.$u.vuex('vuex_qqUnitId', config.result.data[5])
				that.$u.vuex('vuex_qqBannerUnitId', config.result.data[6])
				that.$u.vuex('vuex_compressPicture', config.result.data[7])
				that.$u.vuex('vuex_isShenHe', config.result.data[8])
				that.$u.vuex('vuex_writeExam', config.result.data[9])
				that.$u.vuex('vuex_templateId', config.result.data[10])
				that.$u.vuex('vuex_wxRewardedVideoUnitId', config.result.data[11])
				that.$u.vuex('vuex_bannerUnitId', config.result.data[12])
				that.$u.vuex('vuex_indexUiType', parseInt(config.result.data[13]))
				that.$u.vuex('vuex_appName', config.result.data[14])
			}
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		},
	}
</script>

