	.cu-load.load-modal {
		position: fixed;
		top: 0;
		/*  #ifdef  H5  */
		top: calc(88rpx + constant(safe-area-inset-top));
		top: calc(88rpx + env(safe-area-inset-top));
		/*  #endif  */
		right: 0;
		bottom: 140rpx;
		left: 0;
		margin: auto;
		width: 260rpx;
		height: 260rpx;
		background-color: var(--white);
		border-radius: 10rpx;
		box-shadow: 0 0 0rpx 2000rpx rgba(0, 0, 0, .5);
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		font-size: 28rpx;
		z-index: 9999;
		line-height: 2.4em;
	}

