		position: relative;
		width: 100%;
		height: 4rpx;
		overflow: hidden;
		transition: all .2s ease 0s;
	}

	.load-progress .load-progress-spinner {
		position: absolute;
		top: 10rpx;
		right: 10rpx;
		z-index: 2000;
		display: block;
