		right: 0;
		bottom: 0;
		top: 0;
	}

	.bg-mask cover-view,
	.bg-mask view {
		z-index: 5;
		position: relative;
	}

	.bg-video {
		position: relative;
	}

	.bg-video video {
		display: block;
		height: 100%;
		width: 100%;
		object-fit: cover;
		position: absolute;
		top: 0;
		z-index: 0;
		pointer-events: none;
	}

